const About = () => {
  const [section1st, setSection1st] = useState(null);
  const [section2nd, setSection2nd] = useState(null);
  const [section3rd, setSection3rd] = useState(null);
  const [section4th, setSection4th] = useState(null);
  const [graphFlag, setGraphFlag] = useState(false);
  const [context, setContext] = useState(null);
  const [checkNav, setCheckNav] = useState(false);
  // const [browserSize, setBrowserSize] = useState([0, 0]);

  // sceneInfo.objs.container에 데이터 삽입 됐는지 체크
  const [isFlag, setIsFlag] = useState(false);

  const section1stRef = useRef(null);
  const section2ndRef = useRef(null);
  const section3rdRef = useRef(null);
  const section4thRef = useRef(null);
  const canvasRef = useRef(null);
  const yOffset = useRef(0); // pageYOffset 대신 쓸 변수

  let prevScrollHeight = 0; // 현재 스코롤 위치(YOffset)보다 이전에 위치한 스크롤 섹션들의 스크롤 높이값의 합
  let currentScene = 0; // 현재 활성화 된(눈 앞에 보고 있는) 씬(scroll-section)
  let enterNewScene = false; // 새로운 scene이 시작된 순간 true

  // 각 섹션 정보를 담은 배열
  const sceneInfo = [
    {
      // 0
      type: 'sticky',
      heightNum: 3,
      scrollHeight: 0,
      objs: {
        container: section1st,
        messageA: document.querySelector('#scroll-section-0 .main-message.a'),
        messageB: document.querySelector('#scroll-section-0 .main-message.b'),
        canvas: canvasRef.current,
        context: context,
        videoimg: [],
      },
      values: {
        videoImageCount: 200,
        imgequence: [0, 199],
        canvas_opacity: [1, 0, { start: 0.9, end: 1 }],
        messageA_opacity_in: [0, 1, { start: 0.1, end: 0.2 }],
        messageA_opacity_out: [1, 0, { start: 0.25, end: 0.3 }],
        messageA_translateY_in: [20, 0, { start: 0.1, end: 0.2 }],
        messageA_translateY_out: [0, -20, { start: 0.25, end: 0.3 }],
        messageB_opacity_in: [0, 1, { start: 0.3, end: 0.4 }],
        messageB_opacity_out: [1, 0, { start: 0.45, end: 0.5 }],
        messageB_translateY_in: [20, 0, { start: 0.3, end: 0.4 }],
        messageB_translateY_out: [0, -20, { start: 0.45, end: 0.5 }],
      },
    },
    {
      // 1
      type: 'normal',
      scrollHeight: 0,
      objs: {
        container: section2nd,
      },
    },
    {
      // 2
      type: 'normal',
      // heightNum: 2,
      scrollHeight: 0,
      objs: {
        container: section3rd,
        tlwrapper: document.querySelector('#tlwrapper'),
      },
    },
    {
      // 3
      type: 'sticky',
      heightNum: 1.5,
      scrollHeight: 0,
      objs: {
        container: section4th,
        email: document.querySelector('#scroll-section-3 .email'),
        mobile: document.querySelector('#scroll-section-3 .mobile'),
      },
      values: {
        // email_opacity_in: [0, 1, { start: 0.1, end: 0.2 }],
        // email_opacity_out: [1, 0, { start: 0.25, end: 0.3 }],
        email_scale_in: [1, 2, { start: 0.1, end: 0.2 }],
        email_scale_out: [2, 1, { start: 0.25, end: 0.3 }],
        mobile_scale_in: [1, 2, { start: 0.1, end: 0.2 }],
        mobile_scale_out: [2, 1, { start: 0.25, end: 0.3 }],
      },
    },
  ];

  const setLayout = () => {
    sceneInfo.forEach((item) => {
      if (item.objs.container) {
        if (item.type === 'sticky') {
          item.scrollHeight = item.heightNum * window.innerHeight;
        } else if (item.type === 'normal') {
          item.scrollHeight = item.objs.container.offsetHeight;
        }
        item.objs.container.style.height = `${item.scrollHeight}px`;
      }
    });

    yOffset.current = window.pageYOffset;

    // 페이지 새로고침 시 현재 scene 값 세팅
    let totalScrollHeight = 0;
    for (let i = 0; i < sceneInfo.length; i++) {
      totalScrollHeight += sceneInfo[i].scrollHeight;
      if (totalScrollHeight >= yOffset.current) {
        currentScene = i;
        break;
      }
    }
    let container = document.getElementsByClassName('container');
    if (container[0]) {
      container[0].setAttribute('id', `show-scene-${currentScene}`);
    }

    if (sceneInfo[0].objs.canvas) {
      const heightRatio = window.innerHeight / 1080;
      sceneInfo[0].objs.canvas.style.transform = `translate3d(-50%, -50%, 0) scale(${heightRatio})`;
    }
  };

  // browser resize function
  // const previousSize = usePrevious(browserSize);

  // const updateBrowserSize = () => {
  //   setBrowserSize([window.innerWidth, window.innerHeight]);

  //   if (previousSize !== browserSize) {
  //     setLayout();
  //   }
  // };

  const scrollLoop = () => {
    enterNewScene = false;
    prevScrollHeight = 0;

    for (let i = 0; i < currentScene; i++) {
      prevScrollHeight += sceneInfo[i].scrollHeight;
    }

    if (
      yOffset.current >
      prevScrollHeight + sceneInfo[currentScene].scrollHeight
    ) {
      enterNewScene = true;
      currentScene++;
      let container = document.getElementsByClassName('container');
      if (container[0]) {
        container[0].setAttribute('id', `show-scene-${currentScene}`);
      }
    }

    if (yOffset.current < prevScrollHeight) {
      enterNewScene = true;
      if (currentScene === 0) return; // 브라우저 바운스 효과로 인해 마이너스가 되는 것을 방지 (모바일)
      currentScene--;
      let container = document.getElementsByClassName('container');
      if (container[0]) {
        container[0].setAttribute('id', `show-scene-${currentScene}`);
      }
    }

    if (enterNewScene) return;

    playAnimation();
  };

  const playAnimation = () => {
    const objs = sceneInfo[currentScene].objs;
    const values = sceneInfo[currentScene].values;
    const currentYOffset = yOffset.current - prevScrollHeight;
    const scrollHeight = sceneInfo[currentScene].scrollHeight;
    const scrollRatio = currentYOffset / scrollHeight;

    switch (currentScene) {
      case 0:
        let sequence = Math.round(
          calcValues(values.imgequence, currentYOffset),
        );

        objs.context.drawImage(objs.videoimg[sequence], 0, 0);
        objs.canvas.style.opacity = calcValues(
          values.canvas_opacity,
          currentYOffset,
        );

        setGraphFlag(false);
        if (scrollRatio <= 0.22) {
          // in
          objs.messageA.style.opacity = calcValues(
            values.messageA_opacity_in,
            currentYOffset,
          );
          objs.messageA.style.transform = `translate3d(${calcValues(
            values.messageA_translateY_in,
            currentYOffset,
          )}%)`;
        } else {
          // out
          objs.messageA.style.opacity = calcValues(
            values.messageA_opacity_out,
            currentYOffset,
          );
          objs.messageA.style.transform = `translate3d(${calcValues(
            values.messageA_translateY_out,
            currentYOffset,
          )}%)`;
        }

        if (scrollRatio <= 0.42) {
          // in
          objs.messageB.style.opacity = calcValues(
            values.messageB_opacity_in,
            currentYOffset,
          );
          objs.messageB.style.transform = `translate3d(0, ${calcValues(
            values.messageB_translateY_in,
            currentYOffset,
          )}%, 0)`;
        } else {
          // out
          objs.messageB.style.opacity = calcValues(
            values.messageB_opacity_out,
            currentYOffset,
          );
          objs.messageB.style.transform = `translate3d(0, ${calcValues(
            values.messageB_translateY_out,
            currentYOffset,
          )}%, 0)`;
        }

        break;
      case 1:
        setGraphFlag(false);
        if (scrollRatio > 0.1 && scrollRatio <= 0.12) {
        }
        break;
      case 2:
        if (scrollRatio <= 0.2) {
          setGraphFlag(true);
        }
        break;
      case 3:
        setGraphFlag(false);
        if (scrollRatio <= 0.22) {
          objs.email.style.transform = `scale(${calcValues(
            values.email_scale_in,
            currentYOffset,
          )})`;
          objs.mobile.style.transform = `scale(${calcValues(
            values.mobile_scale_in,
            currentYOffset,
          )})`;
        } else {
          objs.email.style.transform = `scale(${calcValues(
            values.email_scale_in,
            currentYOffset,
          )})`;
          objs.mobile.style.transform = `scale(${calcValues(
            values.mobile_scale_in,
            currentYOffset,
          )})`;
        }
        break;
      default:
        return;
    }
  };

  const calcValues = (values, currentYOffset) => {
    let rv;
    // 현재 씬(스크롤 섹션)에서 스크롤된 범위를 비율로 구하기
    const scrollHeight = sceneInfo[currentScene].scrollHeight;
    const scrollRatio = currentYOffset / scrollHeight;

    if (values.length === 3) {
      // start ~ end 사이에 애니메이션 실행
      const partScrollStart = values[2].start * scrollHeight;
      const partScrollEnd = values[2].end * scrollHeight;
      const partScrollHeight = partScrollEnd - partScrollStart;

      if (
        currentYOffset >= partScrollStart &&
        currentYOffset <= partScrollEnd
      ) {
        rv =
          ((currentYOffset - partScrollStart) / partScrollHeight) *
            (values[1] - values[0]) +
          values[0];
      } else if (currentYOffset < partScrollStart) {
        rv = values[0];
      } else if (currentYOffset > partScrollEnd) {
        rv = values[1];
      }
    } else {
      rv = scrollRatio * (values[1] - values[0]) + values[0];
    }

    return rv;
  };

  const setCanvasimg = () => {
    let imgElme;
    for (let i = 0; i < sceneInfo[0].values.videoImageCount; i++) {
      imgElme = new Image();
      imgElme.src = `static/video/001/scene ${1 + i}.jpg`;
      sceneInfo[0].objs.videoimg.push(imgElme);
    }

    imgElme.onload = () => {
      sceneInfo[0].objs.context.drawImage(sceneInfo[0].objs.videoimg[0], 0, 0);
    };
  };

  const navCheck = () => {
    setCheckNav(true);
    return () => setCheckNav(false);
  };

  useEffect(() => {
    setSection1st(section1stRef.current);
    setSection2nd(section2ndRef.current);
    setSection3rd(section3rdRef.current);
    setSection4th(section4thRef.current);

    if (sceneInfo[0].objs.container) {
      setIsFlag(true);
    }
  }, [sceneInfo]);

  // sceneInfo[]에 context 값 세팅하기 위함
  useEffect(() => {
    const canvas = canvasRef.current;
    const context = canvas.getContext('2d');

    setContext(context);
  });

  useEffect(() => {
    if (isFlag) {
      setLayout();
      setCanvasimg();
    }
  }, [isFlag]);

  useEffect(() => {
    window.addEventListener('resize', setLayout);

    return () => window.removeEventListener('resize', setLayout);
  });

  useEffect(() => {
    window.addEventListener('scroll', () => {
      yOffset.current = window.pageYOffset;

      if (isFlag) {
        scrollLoop();
        navCheck();
      }
    });
    return () => window.removeEventListener('scroll', scrollLoop);
  }, [isFlag]);

  // browser resize
  // useEffect(() => {
  //   window.addEventListener('resize', updateBrowserSize);
  //   return () => window.removeEventListener('resize', updateBrowserSize);
  // }, [browserSize]);

  return (
    <>
      <GlobalStyle />
      <Container className="container">
        {/* 헤더 */}
        <Header checkNav={checkNav} />

        {/* 스크롤 영역 */}
        {/* 섹션 1 */}
        <Section1st
          className="scroll-section"
          id="scroll-section-0"
          ref={section1stRef}
        >
          <h1>안녕하세요</h1>
          <CanvasWrapper className="sticky-elem sticky-elem-canvas">
            <canvas
              id="video-canvas-0"
              ref={canvasRef}
              width="1920px"
              height="1080px"
            />
          </CanvasWrapper>
          <MainMessage className="sticky-elem main-message a">
            <p>
              프론트엔드 개발자 <br />
              김민지입니다
            </p>
          </MainMessage>
          <MainMessage className="sticky-elem main-message b">
            <p>
              이 포트폴리오는 <br />
              - React.js <br />
              - Styled-Component <br />
              를 사용해서 만들었습니다
              <br />
            </p>
          </MainMessage>
        </Section1st>
        {/* 섹션 2 */}
        <Section2nd
          className="scroll-section"
          id="scroll-section-1"
          ref={section2ndRef}
        >
          <div>
            <strong>보유기술</strong>
            <div>
              {SPEC.map((item, i) => {
                return <SpecText key={i}>{item}</SpecText>;
              })}
            </div>
          </div>
        </Section2nd>
        {/* 섹션 3 */}
        <Section3rd
          className="scroll-section"
          id="scroll-section-2"
          ref={section3rdRef}
        >
          <Title>
            <strong>Timeline</strong>
          </Title>
          <TLWrapper id="tlwrapper" graphFlag={graphFlag}>
            {TIMELINE.map((item, i) => {
              return <TimeText key={i}>{item}</TimeText>;
            })}
            <Car className="car" graphFlag={graphFlag}>
              <img src="static/images/bmw.png" alt="car" />
            </Car>
            <DescMessageA graphFlag={graphFlag}>
              <p>(주)이멕스</p>
            </DescMessageA>
            <DescMessageB graphFlag={graphFlag}>
              <p>(주)스위트스팟</p>
            </DescMessageB>
            <BarWrapper>
              <Bar1 className="bar1" graphFlag={graphFlag} />
              <Bar2 className="bar2" graphFlag={graphFlag} />
            </BarWrapper>
          </TLWrapper>
        </Section3rd>
        {/* 섹션 4 */}
        <Section4th
          className="scroll-section"
          id="scroll-section-3"
          ref={section4thRef}
        >
          <div>
            <strong>Conatact</strong>
            <IconWarpper>
              <Email className="email">
                <a
                  href="mailto:blissfullifemj@gmail.com"
                  title="blissfullifemj@gmail.com"
                >
                  <FontAwesomeIcon icon={faEnvelope} />
                </a>
              </Email>
              <Mobile className="mobile">
                <span data-tooltip-text="010-3027-0735">
                  <FontAwesomeIcon icon={faMobileAlt} />
                </span>
              </Mobile>
            </IconWarpper>
          </div>
        </Section4th>

        {/* 푸터 */}
        <Footer />
      </Container>
    </>
  );
};

export default About;
