import React from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
  font-size: 1rem;
  color: lightslategrey;
  height: 3.5rem;
  margin-top: auto;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const Footer = () => {
  return (
    <>
      <Wrapper>© 2020. Minji Kim all rights reserved.</Wrapper>
    </>
  );
};

export default Footer;
